'use client';
import { ShoppingCart, Package, Store, TrendingUp } from 'lucide-react';

const stats = [
  { name: 'Total Orders', value: '128', icon: ShoppingCart, color: 'bg-blue-500' },
  { name: 'Active Products', value: '45', icon: Package, color: 'bg-green-500' },
  { name: 'Total Shops', value: '12', icon: Store, color: 'bg-purple-500' },
  { name: 'Revenue', value: '৳45,200', icon: TrendingUp, color: 'bg-orange-500' },
];

export default function DashboardOverview() {
  return (
    <div>
      <h1 className="text-2xl font-bold text-gray-800 mb-8">Dashboard Overview</h1>
      
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        {stats.map((stat) => (
          <div key={stat.name} className="bg-white p-6 rounded-xl shadow-sm border border-gray-100 flex items-center">
            <div className={`${stat.color} p-3 rounded-lg text-white mr-4`}>
              <stat.icon className="h-6 w-6" />
            </div>
            <div>
              <p className="text-sm font-medium text-gray-500">{stat.name}</p>
              <p className="text-2xl font-bold text-gray-800">{stat.value}</p>
            </div>
          </div>
        ))}
      </div>

      {/* Recent Activity / Charts could go here */}
      <div className="bg-white p-6 rounded-xl shadow-sm border border-gray-100">
        <h2 className="text-lg font-bold text-gray-800 mb-4">Recent Activity</h2>
        <div className="space-y-4">
          {[1, 2, 3].map((i) => (
            <div key={i} className="flex items-center justify-between py-3 border-b border-gray-50 last:border-0">
              <div className="flex items-center">
                <div className="h-10 w-10 rounded-full bg-gray-100 flex items-center justify-center mr-3">
                   <ShoppingCart className="h-5 w-5 text-gray-600" />
                </div>
                <div>
                  <p className="text-sm font-medium text-gray-800">New Order #ORD-12{i}</p>
                  <p className="text-xs text-gray-500">2 minutes ago</p>
                </div>
              </div>
              <p className="text-sm font-bold text-gray-800">৳1,200</p>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
